<template><section><h2>Mars3D Demo</h2>
<p>When vue-cesium uses Mars3D of Mars Technology to develop, you only need to specify the address of the <strong>directory</strong> of the mars3d library through the configuration item <code v-pre>cesiumPath</code> when VueCesium is introduced.</p>
<pre v-pre><code v-pre class="language-js">Vue.use(VueCesium, {
  <span class="hljs-attr">cesiumPath</span>: <span class="hljs-string">'https://mars3d.cn/lib/'</span>
})

<span class="hljs-comment">// Or specify the address of `cesiumPath` as the **directory** of the mars3d library on the `vc-viewer` component.</span>
<span class="hljs-keyword">import</span> { Viewer } <span class="hljs-keyword">from</span> <span class="hljs-string">'vue-cesium'</span>
Vue.use(Viewer, {
  <span class="hljs-attr">cesiumPath</span>: <span class="hljs-string">'https://mars3d.cn/lib/'</span>
})
</code></pre>
<p>If <code v-pre>vc-viewer</code> is loaded successfully, it will return {Cesium, viewer, map }, and you can use mars3d API for related development through this <code v-pre>map</code>, as shown in the following example:</p>
<h2>Example</h2>
<h3>Use VueCesium to load Mars Technology Mars3D</h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer" ref="viewerContainer">
      <vc-viewer ref="vcViewer" :cesiumPath="cesiumPath" :animation="animation" :timeline="timeline" :fullscreenButton="fullscreenButton" :fullscreenElement="fullscreenElement" @ready="onViewerReady" @leftClick="onLeftClick">
        <vc-navigation></vc-navigation>
        <vc-entity ref="entity" @click="onEntityClick" :position="{lng: 108, lat: 32}" :point="point" :label="label">
          <vc-graphics-billboard ref="billboard" image="https://zouyaoji.top/vue-cesium/favicon.png"></vc-graphics-billboard>
          <vc-graphics-rectangle :coordinates="[130, 20, 80, 25]" material="green"></vc-graphics-rectangle>
        </vc-entity>
        <!-- 天地图注记 -->
        <vc-layer-imagery :sortOrder="20">
          <vc-provider-imagery-tianditu mapStyle="cva_c" token="436ce7e50d27eede2f2929307e6b33c0"></vc-provider-imagery-tianditu>
        </vc-layer-imagery>
        <!-- 天地图影像 -->
        <vc-layer-imagery :sortOrder="10">
          <vc-provider-imagery-tianditu mapStyle="img_c" token="436ce7e50d27eede2f2929307e6b33c0"></vc-provider-imagery-tianditu>
        </vc-layer-imagery>
      </vc-viewer>
      <div class="demo-tool">
        <span>Animation</span>
        <md-switch v-model="animation"></md-switch>
        <span>Timeline</span>
        <md-switch v-model="timeline"></md-switch>
        <span>BaseLayerPicker</span>
        <md-switch v-model="baseLayerPicker"></md-switch>
        <span>FullscreenButton</span>
        <md-switch v-model="fullscreenButton"></md-switch>
        <span>InfoBox</span>
        <md-switch v-model="infoBox"></md-switch>
        <md-button class="md-raised md-accent" @click="unload">Unload</md-button>
        <md-button class="md-raised md-accent" @click="load">Load</md-button>
        <md-button class="md-raised md-accent" @click="reload">Reload</md-button>
      </div>
    </div>
  </template>
  
</doc-preview>
